import React from "react";
import "./css/About.css";
export default function About() {


  return (
    <>
      <div className="map-section">
        <iframe
          className="google-map map-section__map"
          src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d10341.501607079746!2d19.926954!3d49.609527!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x4966422d56a0ed4e!2sKacztex%20Hurtownia%20Odzie%C5%BCy%20Sklep%20Odzie%C5%BCowy!5e0!3m2!1spl!2spl!4v1585333089422!5m2!1spl!2spl"
          frameborder="0"
          style={{ border: 0 }}
          allowfullscreen=""
          aria-hidden="false"
          tabindex="0"
        ></iframe>
        <div className=" map-section__text">
          <div>
            <i class="fas fa-location-arrow"></i>
          </div>
          <div >
            <span className="about-column__big-text">Gdzie jestemy?</span>
            Krawiectwo Konfekcyjne kacztex
            <br />
            34-713 Skawa
            <br />
            Skawa 558A
          </div>
        </div>
      </div>
      <div className="about-column">
        <div className="about-column__divider"></div>
        <div className="about-column__divider">
          <span className="about-column__big-text">O nas</span>
          <br></br>
          Dbamy o sta, wysok jako naszych wyrobw, szybko reagujemy na
          trendy i zmiany pojawiajce si w modzie. Nasze kolekcje oparte s na
          wasnych projektach, a charakteryzuj je elegancja, rnorodno i
          niepowtarzalno fasonw.
        </div>
      </div>

      <div className="about-column ">
        <div className="about-column__divider about-column--photo"></div>
        <div className="about-column__divider about-column--text">
          <span className="about-column__big-text">wYSOKA JAKO</span>
          Naszym celem jest projektowanie ubiorw wygodnych, a zarazem
          eleganckich i szykownych. Staramy si czy to, co w odziey
          klasyczne, z odrobin stylu odzieowego lub sportowego. Szczegln
          uwag przywizujemy do wykoczenia wyrobw. Wiemy, e wanie dbao
          o szczegy decyduje o ostatecznym efekcie i jednoczenie ma istotny
          wpyw na trwao odziey.
        </div>
      </div>

      <div className="about-column">
        <div className="about-column__divider aboutimg"></div>
        <div className="about-column__divider">
          <span className="about-column__big-text">Lata dowiadczenia</span>
          <br></br>
          Istniejemy od 1997 roku i jestemy gotowi, aby sta si wiodcym
          graczem na wiatowym rynku mody oraz udowodni, e ubrania niedrogie
          mog odpowiada najnowszym trendom. Za porednictwem mediw
          spoecznociowych chcemy pokazywa Ci najnowsze i zawsze aktualne
          trendy oraz modowe inspiracje.
        </div>
      </div>
    </>
  );
}
